
.content {
    display: flex;
    flex-wrap: wrap;

}

.card {
    border: 1px solid green;
    height: 200px;
    width: 20%;
    padding: 10px;
    box-sizing: border-box;
}

.card_item {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: red;
    height: 100%;
}

@media screen and (max-width: 1024px) {
    .card {
        width: 33%;
    } 
}

@media screen and (max-width: 768px) {
    .card {
        width: 50%;
    } 
}

@media screen and (max-width: 420px) {
    .card {
        width: 100%;
    } 
}

.container {
    display: flex;
    margin-bottom: 20px;
}

.burger {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 30px;
    padding: 5px;
    border: 1px solid black;
    margin-left: 20px;
    position: relative;
}

.line {
    height: 3px;
    background-color: #000;
}

.menu {
    display: flex;
    align-items: center;
}

.menu_item {
    margin-left: 20px;
    padding: 10px;
    font-size: 20px;
}

@media screen and (max-width: 1024px) {
    .menu {
        display: none;
        position: absolute;
        flex-direction: column;
        background-color: rgb(182, 168, 168);
        color: blanchedalmond;
        top: 5%;
        left: 8%;
    } 
    .burger:hover+.menu {
        display: flex;
        flex-direction: column;
        position: absolute;
        
    }
}

@media screen and (min-width: 1024px) {
    .burger {
        display: none;
    } 
}